
.projectsContainer {
    width: 100%;
    min-height: 100vh;
    position: relative;
    margin: 0;
    padding: 0;
    background-image: url("../images/concrete.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}


.projectsTitle {
    font-family: 'Figtree', sans-serif;
    position: relative;
    color: cornsilk;
    font-family: 'Kanit', sans-serif;
    font-weight: 800;
    top: 2.5rem;
    margin-left: 5px;
    /* font-size: 2rem; */
    font-size: clamp(2rem, 1rem + 5vw, 8rem);
}
