.projectsBtns {
    position: absolute;
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    margin-left: 10px;
}
    @media screen and (min-width: 1440px) {
        .projectsBtns {
            margin-left: 20px;
    }
}
    @media screen and (min-width: 2560px) {
        .projectsBtns {
            margin-left: 50px;
    }
}


.tttBtn {
    background: transparent;
    color: cornsilk;
    font-family: 'Kanit', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    border: 1px solid cornsilk;
    border-radius: 5px;
    padding: .15rem .5rem;
    background-color: rgb(40, 40, 40, 0.6);
    margin-top: 10px;
}
    @media screen and (min-width: 1440px) {
        .tttBtn {
            font-size: 2rem;
            padding: .3rem 3rem;
    }
}
    @media screen and (min-width: 2560px) {
        .tttBtn {
            font-size: 3rem;
            border: 3px solid cornsilk;
    }
}

.tttBtn:hover {
    color: #F7EF8A;
    border: 1px solid #F7EF8A;
    transform: scale(1.1);
}


