
.skillsContainer {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #ede5dd;
  margin: auto;
  background-image: url("../images/Nsnap4.jpg");
  background-size: cover;
  overflow: hidden;
}


.skillsTitle {
  position: relative;
  text-align: center;
  color: cornsilk;
  top: 4%;
  font-family: 'Fira Sans', sans-serif;
  font-size: 4rem;
  margin: 0;
  padding: 0;
}
  @media screen and (min-width: 327px) {
    .skillsTitle {
      font-size: 8rem;
  }
}


@media screen and (min-width: 768px) {
  #rotate {
    position: absolute;
    font-size: 14rem;
    margin: 0;
    padding: 0;
    top: 50%;
    transform: translateX(-25%) translateY(-50%) rotate(-90deg);
  }
}
@media screen and (min-width: 1440px) {
  #rotate {
    font-size: 20rem;
  }
}
@media screen and (min-width: 2560px) {
  #rotate {
    font-size: 30rem;
  }
}


@media screen and (max-width: 767px) {
  .skillsTitleTwo {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .skillsTitleTwo {
    position: absolute;
    color: cornsilk;
    font-family: 'Fira Sans', sans-serif;
    font-size: 14rem;
    margin: 0;
    padding: 0;
    right: 0;
    top: 50%;
    transform: scale(-1, 1) translateX(-25%) translateY(-50%) rotate(-90deg);
  }
}
@media screen and (min-width: 1440px) {
  .skillsTitleTwo {
    font-size: 20rem;
  }
}
@media screen and (min-width: 2560px) {
  .skillsTitleTwo {
    font-size: 30rem;
  }
}


.windowContainer {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 2;
}


.window {
  position: relative;
  width: 278px;
  height: 400px;
  border: 2px solid cornsilk;
  overflow: hidden;
  overscroll-behavior: contain;
}
  @media screen and (min-width: 377px) {
    .window {
      width: 300px;
      height: 500px;
  }
}
  @media screen and (min-width: 768px) {
    .window {
      width: 400px;
      height: 70vh;
      margin-top: 30px;
  }
}
  @media screen and (min-width: 1440px) {
    .window {
      width: 550px;
      height: 50vh;
      margin-top: 30px;
  }
}

@media screen and (min-width: 2560px) {
  .window {
    width: 1000px;
    height: 75vh;
    margin-top: 30px;
  }
}



.bottomArrow {
  position: absolute;
  right: 10px;
  bottom: 20px;
  z-index: 3;
}

